<template>
  <div class="background">
    <div class="window">
      <button @click="closeWindow()">Close</button>
      <h1>Add multiple users to {{ subject.code }}</h1>

      <textarea
        v-model="users"
        placeholder="Paste emails separated by new lines"
      >
      </textarea>
      <button :disabled="users == null" @click="submit">Submit</button>
    </div>
  </div>
</template>
<script>
import axios from "axios";

export default {
  props: {
    subject: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      error: null,
      users: "",
    };
  },

  created() {
    document.title = "QS99 - Login";
  },

  methods: {
    //Method for submitting form
    submit() {
      axios
        .post(
          "http://localhost:8001/subject/addUsers",
          { data: this.users },
          {
            params: {
              subjectId: this.subject.id,
            },
          }
        )
        .then(() => {
          this.closeWindow();
        })
        .catch((err) => {
          alert(err);
        });
    },

    closeWindow() {
      this.$emit("closeWindowMultipleUsers");
    },
  },

  computed: {},
};
</script>

<style>
.background {
  width: 100%;
  height: 100%;
  background-color: white;
}
.window {
  position: absolute;
  background-color: antiquewhite;
  border: solid;
  border-radius: 3px;
  padding: 50px;
  top: 123px;
}
</style>
